<template>
  <div class="mv-container">
    <!-- mv详情 -->
    <div class="mv-wrap" style="margin-right: 35px;">
      <h3 class="title">mv详情</h3>
      <!-- mv -->
      <div class="video-wrap">
        <video controls :src="url"></video>
      </div>
      <!-- mv详情信息 -->
      <div class="info-wrap">
        <div class="singer-info">
          <div class="avatar-wrap">
            <!-- 头像 -->
            <img :src="icon" alt="">
          </div>
            <!-- 歌手名字 -->
          <span class="name">{{ mvInfo.artistName }}</span>
        </div>
        <div class="mv-info">
            <!-- 标题 -->
          <h2 class="title"> {{ mvInfo.name }}</h2>
          <span class="date"> 发布: {{ mvInfo.publishTime }}</span>
            <!-- 播放次数 -->
          <span class="number">播放:{{ mvInfo.playCount }}次</span>
            <!-- 对mv的描述 -->
          <p class="desc">{{mvInfo.desc}}</p>
        </div>
      </div>
      <!-- 精彩评论 -->
      <div class="comment-wrap" v-if="hotComments !== undefined && hotComments.length !== 0">
        <p class="title"> 精彩评论<span class="number">{{hotComments.length}}</span></p>
        <div class="comments-wrap">
          <div class="item" v-for="(item,index) in hotComments" :key="index">
            <div class="icon-wrap">
              <!-- 头像 -->
              <img :src="item.user.avatarUrl" alt="">
            </div>
            <div class="content-wrap">
              <div class="content">
                <!-- 评论者名字 -->
                <span class="name">{{item.user.nickname}}</span>
                <span class="comment">{{item.content}}</span>
              </div>
              <!-- 回复 -->
              <div class="re-comment">
                <span class="name">{{item.beReplied[0].user.nickname}}</span>
                <span class="comment">{{item.beReplied[0].content}}</span>
              </div>
              <!-- 发表时间 -->
              <div class="date">{{item.time}}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 最新评论 -->
      <div class="comment-wrap">
        <p class="title"> 最新评论<span class="number">{{total}}</span></p>
        <div class="comments-wrap">
          <div class="item" v-for="(item,index) in comments" :key="index">
            <div class="icon-wrap">
              <img :src="item.user.avatarUrl" alt="">
            </div>
            <div class="content-wrap">
              <div class="content">
                <!-- 评论者名字 -->
                <span class="name">{{item.user.nickname }}</span>
                <span class="comment">{{item.content}}</span>
              </div>
              <!-- 回复 -->
              <div class="re-comment" v-if="item.beReplied.length !== 0">
                <span class="name">{{item.beReplied[0].user.nickname}}</span>
                <span class="comment">{{item.beReplied[0].content}}</span>
              </div>
              <!-- 发表时间 -->
              <div class="date">{{item.time }}</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 分页 -->
      <el-pagination
        background
        layout="prev, pager, next"
        :total="100"
        size="10"
      >
      </el-pagination>
    </div>
     <!-- 相关推荐 -->
    <div class="mv-recommend">
      <h3 class="title">相关推荐</h3>
      <div class="mvs">
        <div class="items">
          <div class="item" v-for="(item,index) in simiMvs" :key="index">
            <div class="img-wrap">
              <img :src="item.cover" alt="">
                <span class="iconfont icon-play"></span>
                <div class="num-info">
                  <div class="iconfont icon-play"></div>
                  <div class="num">{{item.playCount}}</div>
                </div>
                <span class="time">{{item.duration}}</span> 
            </div>
            <div class="info-wrap">
              <!-- mv标题 -->
              <div class="name">{{item.name}}</div>
              <!-- 歌手 -->
              <div class="singer">{{item.artistName}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      id: '',
      // 总条数
      total: 20,
      // 页码
      pageNum: 1,
      // 页容量
      pageSize: 10,
      // mv地址
      url: '',
      mvInfo: {},
      simiMvs: [],
      icon: '',
      hotComments: [],
      comments: []
    }
  },
  created () {
    this.getMvUrl()
    this.getMvDetail()
    this.getMvComment()
    this.getSimiMv()
  },
  methods: {
    //获取mv地址
    getMvUrl () {
      this.FFaxios({
        url: 'https://autumnfish.cn/mv/url',
        method: 'get',
        params: {
          id: this.$route.query.id
        }
      }).then(res => {
        // console.log(res);
        this.url = res.data.data.url
      })
    },
    //mv信息
    getMvDetail () {
      this.FFaxios({
        url: 'https://autumnfish.cn/mv/detail',
        method: 'get',
        params: {
          mvid: this.$route.query.id
        }
      }).then(res => {
        // console.log(res);
        //mv的信息
        this.mvInfo = res.data.data
        //歌手的信息
        this.FFaxios({
          url: 'https://autumnfish.cn/artists',
          method: 'get',
          params: {
            id: this.mvInfo.artists[0].id
          }
        }).then(res => {
          // console.log(res);
          // console.log(res.data.artist.picUrl);
          //歌手的封面信息
          this.icon = res.data.artist.picUrl
        })
      })
    },
    //获取评论数据
    getMvComment () {
      this.FFaxios({
        url: 'https://autumnfish.cn/comment/mv',
        method: 'get',
        params: {
          id: this.$route.query.id,
          limit: 10,
          offest: 0
        }
      }).then(res => {
        // console.log(res);
        // console.log(res.data.comments);
        // console.log(res.data.hotComments);
        // console.log(res.data.total);
        this.comments = res.data.comments
        this.hotComments = res.data.hotComments
        this.total = res.data.total
      })
    },
    //获取相关推荐
    getSimiMv () {
      this.FFaxios({
        url: 'https://autumnfish.cn/simi/mv',
        method: 'get',
        params: {
          mvid: this.$route.query.id
        }
      }).then(res => {
        console.log(res);
        this.simiMvs = res.data.mvs
      })
    }

  }
}
</script>
<style lang='scss'>
.mv-container {
  display: flex;
  .mv-wrap {
    width: 700px; /* 694px比较合适 */
    .title {
      margin-bottom: 20px;
    }
    .video-wrap {
      width: 100%;
      height: 390px;
      margin-bottom: 20px;
      video {
        border-radius: 5px;
        height: 100%;
        width: 100%;
        outline: none;
      }
    }
    .info-wrap {
      margin-bottom: 50px;
      .singer-info {
        display: flex;
        align-items: center;
        margin-bottom: 35px;
        .avatar-wrap {
          width: 70px;
          height: 70px;
          border-radius: 50%;
          overflow: hidden;
          margin: 0 10px 0 0;
          img {
            width: 100%;
            height: 70px;
          }
        }
      }
      .mv-info {
        .title {
          font-size: 30px;
        }
        .date {
          color: #bebebe;
          font-size: 14px;
          margin-right: 35px;
        }
        .number {
          color: #bebebe;
          font-size: 14px;
        }
        .desc {
          font-size: 15px;
          margin-top: 20px;
        }
      }
    }
    .comment-wrap {
      margin-bottom: 70px;
      .title {
        font-size: 25px;
        .number {
          font-size: 20px;
          color: black;
        }
      }
      .comments-wrap {
        .item {
          display: flex;
          padding-top: 20px;
          .icon-wrap {
            margin-right: 15px;
            img {
              width: 50px;
              height: 50px;
              border-radius: 50%;
            }
          }
          .content-wrap {
            border-bottom: 1px solid #f2f2f1;
            .content {
              margin-bottom: 10px;
              .name {
                color: #517eaf;
                font-size: 14px;
                margin-right: 5px;
              }
              .comment {
                font-size: 14px;
              }
            }
            .re-content {
              margin-bottom: 10px;
              padding: 10px;
              background-color: #e6e5e6;
              border-radius: 5px;
              .name {
                color: #517eaf;
                font-size: 14px;
                margin-right: 5px;
              }
              .comment {
                font-size: 14px;
              }
            }
            .date {
              color: #bebebe;
              font-size: 14px;
              margin-right: 25px;
              margin-bottom: 10px;
            }
          }
        }
      }
    }
    .el-pagination {
      margin-top: 50px;
      text-align: center;
    }
  }

  .mv-recommend {
    flex: 1;
    .title {
      margin-bottom: 20px;
    }
    .mvs {
      .items {
        display: flex;
        flex-wrap: wrap;
        .item {
          cursor: pointer;
          width: 100%;
          display: flex;
          align-items: center;
          padding: 10px;
          &:hover {
            background-color: #f5f5f5;
          }
          .img-wrap {
            width: 180px;
            position: relative;
            margin-right: 10px;
            &:hover {
              .icon-play {
                opacity: 1;
              }
            }
            img {
              width: 100%;
              border-radius: 5px;
            }
            .icon-play {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              display: flex;
              justify-content: center;
              align-items: center;
              width: 40px;
              height: 40px;
              background: rgba(255, 255, 255, 0.8);
              color: #dd6d60;
              border-radius: 50%;
              opacity: 0;
              &::before {
                transform: translateX(1px);
              }
            }
            .num-info {
              position: absolute;
              color: white;
              top: 0;
              right: 0;
              display: flex;
              align-content: center;
              font-size: 15px;
              padding-right: 5px;
              padding-top: 2px;
              .icon-play {
                font-size: 12px;
                display: flex;
                padding-right: 70px;
                align-items: center;
                margin-right: 15px;
                background: none;
                color: white;
                opacity: 1;
              }
              .num {
              }
            }
            .time {
              position: absolute;
              right: 5px;
              bottom: 5px;
              color: white;
            }
          }
          .info-wrap {
            flex: 1;
            .name {
              font-size: 15px;
            }
            .singer {
              font-size: 15px;
              color: gray;
            }
          }
        }
      }
    }
  }
}
</style>